const QA = ({children} )=>
{
    const data = [0,0,0,0,0];
    const dataList = data.map((data, index)=>
    {
        return(
        <tbody style={{width:1280,height:101, fontSize:12}} key ={index}>
            <tr>
                <td style={{}}>    답변완료    </td>
                <td style={{ borderBottom : "1px solid #ddd", width : 830, height : 101, cursor:"pointer"}}>
                    <img src ={img} align = "left" alt ="iconiamge"/>

                    <Link to="/qa" style={{textDecoration:"none", color :"black" }} >
                        <p>GOBI 고비 캐시미어 머플러 베이직/몽골캐시미어100% 여자 남자 부모님   </p>
                    </Link>
                        <p> 비밀글입니다 </p>
                </td>
                <td style={td15}>    2018.11.31.    </td>
                <td style={td15}>    74    </td>
            </tr>
        </tbody>
    )})



    return(
        <Layout>

        <div style={{width:1280, margin :"auto"}}>

        <div style={{ position:"relative", left:40, marginTop :40}}>
            <h4> 묻고 답하기 </h4>
        </div>


        <table style={table}>
            <thead style={{fontWeight:"bold", textAlign:"center" }}>
                <tr>
                    <td style={{width:150,height:59,borderBottom : "1px solid #ddd"}}>    구분    </td>
                    <td style={{width:830,height:59,borderBottom : "1px solid #ddd"}}>    제목    </td>
                    <td style={{width:150,height:59,borderBottom : "1px solid #ddd"}}>    작성일    </td>
                    <td style={{width:150,height:59,borderBottom : "1px solid #ddd"}}>    조회수    </td>
                </tr>
            </thead>


            {dataList}

        </table>



        <div style={{ marginTop:50,textAlign:"center"}}>
                <Pagination count={9} link={""} />
        </div>

        <div style={{textAlign:"center"}}>

            {/* <input type='text'style={{width:240, height:40 }} />
            <button style={{width:80.33, height:45}} > 검색</button> */}
        </div>
        </div>

        </Layout>
    )
}



export default QA